$(window).on('load resize', function(){
    if ($(window).width() <= 992) {
        $('.catalog-item__title').insertBefore('.catalog-item__inner');
        $('.catalog__top').insertBefore('.catalog__inner');
    } else{
        $('.catalog-item__title').insertBefore('.catalog-item__info');
        $('.catalog__top').insertBefore('.catalog-sort');
    }
});
