$("._menu-item").hover(onIn, onOut);

function onIn() {
    if (window.innerWidth > 992) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                el.addClass("_show")
            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 992) {
        $(this).removeClass("_show")
    }
}

$(".nav__show").click(function () {
    if (window.innerWidth <= 992){
        $(this).parents(".nav__item").toggleClass("_show")
        $(this).siblings(".nav__hidden").slideToggle()
    }
})

$(".catalog-btn").click(function () {
    $(".header").addClass("_open")
    $(".m-page").addClass("fixed-body")
})

$(".menu__close").click(function () {
    $(".header").removeClass("_open")
    $(".m-page").removeClass("fixed-body")
})
