if(document.getElementById("map")){
    ymaps.ready(init);
}

let coord = [55.745864, 37.687698]
let mapPoints = [
    {
        title: "Заголовок",
        cords: [55.676746, 37.893325]
    },
    {
        title: "Заголовок",
        cords: [55.713185, 37.844621]
    },
    {
        title: "Заголовок",
        cords: [55.591119, 37.752956]
    },
    {
        title: "Заголовок",
        cords: [55.827383, 37.840701]
    },
]



function init() {
    let myMap = new ymaps.Map("map", {
        center: coord,
        zoom: 10,
        controls: []
    });

    myMap.behaviors.disable('scrollZoom');


    mapPoints.forEach((el,index) => {
        let placeMark = new ymaps.Placemark(el.cords, {
                balloonContentHeader: `${el.title}`,
                balloonContentBody: ``,
                balloonContentFooter: '',
                hintContent: '',
            },
            {
                iconLayout: 'default#image',
                iconImageHref: "img/mark.svg",
                iconImageSize: [50, 50],
            },
        );
        myMap.geoObjects.add(placeMark);

    })




}
